@import "sanitize";

body {
  font-family: "Avenir Next", "Segoe UI", "Helvetica Neue", Arial, "Hiragino Sans GB", "PingFang SC", "Heiti SC", "Microsoft YaHei UI", "Microsoft YaHei", "Source Han Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 80vh;
  width: 100%;
  padding: 3em;
  text-align: center;
  color: #fff;
  background-image: linear-gradient(180deg, #69D2FD 0%, #38A6F9 100%);

  .title {
    font-size: 36px;
    font-weight: lighter;
    margin-bottom: .3em;
  }
}

.service {
  width: 80%;
  margin: 0 auto;
  margin-top: -36%;
  margin-bottom: 3em;
  background-color: #fff;
  padding: 1em;
  box-shadow: #E2E2E2 0px 0px 8px;
  border-radius: 2px;

  header {
    text-align: center;
    font-size: 32px;
    color: #38A6F9;
    font-style: italic;
  }

  .item {
    margin-bottom: 2em;

    .cover {
      display: block;
      width: 28%;
      margin: 0 auto;
    }
  }

  p {
    text-align: center;
    color: #808080;
  }
}

.footer {
  background-color: #fff;
  width: 100%;
  text-align: center;
  padding: 1em 0;
  font-size: 14px;
  border-top: 1px solid #ececec;

  a {
    color: #38A6F9;
  }
}
